import React,{useState, useEffect} from 'react';
import { Link, useNavigate} from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleRight, faEye, faEyeSlash} from '@fortawesome/free-solid-svg-icons';
import '../style/form.css';

function Login(props) {
  // 아이디
  const [bizId, setBizId] = useState('');
  const [idError, setIdError] = useState('');
  //비밀번호
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  // 아이디 저장
  const [saveId, setSaveId] = useState(false);
  const navigate = useNavigate();

  // 아이디
  const handleIdChange = (e) => {
    // 숫자만 남기기
    const value = e.target.value.replace(/\D/g, '');
    setBizId(value);

    // 10자리 체크
    if(value.length !== 10){
      setIdError('사업자등록번호 10자리를 입력해주세요.');
    } else {
      setIdError('');
    }
  };

  const togglePassword = () => {
    setShowPassword(!showPassword);
  };

  // 페이지 로딩시 저장된 아이디 불러오기
  useEffect(()=>{
    const saveId = localStorage.getItem('savedBizId');
    if(saveId){
      setBizId(saveId);
      setSaveId(true);
    };
  }, []);

// 로그인 처리
const handleLogin = (e) => {
  e.preventDefault();

  const users = JSON.parse(localStorage.getItem('users') || '[]');
  const user = users.find(u => u.businessNumber === bizId && u.password === password);

  if (user) {
    alert('로그인이 완료되었습니다.');
    localStorage.setItem('isLogin', 'true');
    localStorage.setItem('userName', user.ceo); // 사용자명 저장
    navigate('/allra-assignment--CHB-');
  } else {
    alert('사업자번호 또는 비밀번호가 틀렸습니다.');
  }
};
  return (
    <section className='login'>
      <div className='login-header'>
        <div className='login-header-inner'>
          <p>
            <Link to ='/allra-assignment--CHB-'>홈</Link> <FontAwesomeIcon  icon={faAngleRight}/> <Link to='/login'>로그인</Link>
          </p>
        </div>
      </div>
      
      <form className='login-form' onSubmit={handleLogin}>
        <h2>로그인</h2>
        <div>
          <p>
            <label htmlFor='bizId'>사업자등록번호(ID로 사용돼요)</label>
          </p>
          <p>
            <input
            type="text"
            id="bizId"
            placeholder="-제외 10자리 입력"
            value={bizId}
            onChange={handleIdChange}
            maxLength={10} // 10자리 제한
            />
          {idError && <p className='error-msg'>{idError}</p>}
          </p>
        </div>

        <div>
          <p>
            <label>비밀번호</label>
          </p>
          <p style={{ position: 'relative',  }}>
            <input 
            type={showPassword ? 'text' : 'password'}
            placeholder="비밀번호 입력해주세요."
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            />
            <span
            onClick={togglePassword}
            style={{position: 'absolute',  right: '30px', top: '50%',
              transform: 'translateY(-50%)', cursor: 'pointer', color: '#dfe3e8'
            }}
            >
            <FontAwesomeIcon icon={showPassword ? faEyeSlash : faEye} />
            </span>
          </p>
        </div>

        <p className='idsave'>
          <input type='checkbox'
          checked={saveId}
          onChange={(e)=>setSaveId(e.target.checked)}
          />
          <label>아이디저장</label>
        </p>

        <div className='login-join-btn'>
          <p>
          <button type='submit' className='login-btn'>로그인</button>
          </p>
          <Link to='/join' title='회원가입하기' className='join-btn'>회원가입</Link>
        </div>
      </form>
    </section>
  );
}

export default Login;