import { Link, useNavigate } from 'react-router-dom';

function Header(props) {
  const navigate = useNavigate();
  const isLogin = localStorage.getItem('isLogin') === 'true';
  const userName = localStorage.getItem('userName');

  const handleLogout = () => {
    localStorage.removeItem('isLogin');
    localStorage.removeItem('userName');
    navigate('/allra-assignment--CHB-');
  };

  return (
    <header>
      <div className='header-container'>
        <h1>
          <Link to='/allra-assignment--CHB-' title='메인으로 바로가기'>
            <img src={`${process.env.PUBLIC_URL}/images/allra_header_logo.svg`} alt='메인으로 이동하기' />
          </Link>
        </h1>

        <div className='auth-form'>
          {isLogin ? (
            <>
              <span><strong>{userName}</strong>님</span>
              <button onClick={handleLogout} className='logout-btn'>로그아웃</button>
            </>
          ) : (
            <>
              <Link to='/join' title='회원가입 하러가기'>회원가입</Link>
              <Link to='/login' title='로그인 하러가기'>로그인</Link>
            </>
          )}
        </div>
      </div>
    </header>
  );
}

export default Header;